<template>
	<div class="alert alert-secondary">
		<h3>: {{stats.success}}</h3>
		<h3>: {{stats.error}}</h3>
		<h3>: {{stats.time}}</h3>
		<hr>
		<div class="buttons">
			<button class="btn btn-primary" @click="$emit('repeat')"></button>
			<button class="btn btn-primary" @click="$emit('nextLevel')" v-if="level < 2"></button>
			<button class="btn btn-success" @click="$emit('eraseGame')"></button>
		</div>				
	</div>
</template>

<script>
export default {
	props:['stats','level']
}
	
</script>

<style scoped>
	
.buttons{
	display: flex;
	justify-content: space-around;
}
</style>


